import React from "react";
import "./GithubRepoCard.css";
import { Fade } from "react-reveal";

export default function GithubRepoCard({ repo, theme }) {
  function openInNewTab(url) {
    if (!url) return;
    const win = window.open(url, "_blank");
    win.focus();
  }

  const isValidUrl = (url) => url && url.trim() !== "";

  return (
    <div className="repo-card-div" style={{ backgroundColor: theme.highlight }}>
      <Fade bottom duration={2000} distance="40px">
        <div key={repo.id}>
          <div className="repo-name-div">
            {isValidUrl(repo.appLogo) ? (
              <img src={repo.appLogo} alt="App Logo" className="app-logo" />
            ) : (
              <svg
                aria-hidden="true"
                className="octicon repo-svg"
                height="16"
                role="img"
                viewBox="0 0 12 16"
                width="12"
              >
                <path
                  fillRule="evenodd"
                  d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 
                  .55-.45 1-1 1H6v2l-1.5-1.5L3 
                  16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 
                  1-1h10c.55 0 1 .45 1 1zm-1 
                  10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"
                ></path>
              </svg>
            )}

            <p className="repo-name" style={{ color: theme.text }}>
              {repo.name}
            </p>
          </div>

          <p className="repo-description" style={{ color: theme.text }}>
            {repo.description}
          </p>

          <div className="repo-stats">
            <ul className="social">
              {isValidUrl(repo.appStoreUrl) && (
                <a
                  href={repo.appStoreUrl}
                  className="store-button apple"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <span className="store-icon"></span>
                  <div className="store-text">
                    <span className="small-text">Download on the</span>
                    <span className="large-text">App Store</span>
                  </div>
                </a>
              )}

              {isValidUrl(repo.playStoreUrl) && (
                <li
                  data-tooltip="Play Store"
                  style={{ "--bg": "#3a83f7" }}
                  onClick={() => openInNewTab(repo.playStoreUrl)}
                  role="button"
                  tabIndex={0}
                >
                  <a
                    href={repo.playStoreUrl}
                    className="store-button google"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span className="store-icon">
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 331 378"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M154.39 180.528L1.87172 346.061C1.88604 346.091 1.88604 346.135 1.90037 346.164C6.58465 364.138 22.6287 377.366 41.681 377.366C49.3019 377.366 56.4501 375.257 62.5812 371.565L63.0683 371.272L234.74 269.975L154.39 180.528Z"
                          fill="#EA4335"
                        />
                        <path
                          d="M308.684 152.341L308.541 152.238L234.423 108.306L150.922 184.29L234.724 269.957L308.441 226.464C321.362 219.316 330.143 205.385 330.143 189.329C330.143 173.362 321.491 159.504 308.684 152.341Z"
                          fill="#FBBC04"
                        />
                        <path
                          d="M1.86896 31.8844C0.952154 35.3416 0.465103 38.9745 0.465103 42.7247V335.236C0.465103 338.986 0.952154 342.619 1.88328 346.061L159.631 184.761L1.86896 31.8844Z"
                          fill="#4285F4"
                        />
                        <path
                          d="M155.517 188.977L234.448 108.276L62.9771 6.61221C56.7457 2.78883 49.4686 0.591488 41.6901 0.591488C22.6378 0.591488 6.56512 13.8488 1.88083 31.8378C1.88083 31.8524 1.86652 31.8671 1.86652 31.8817L155.517 188.977Z"
                          fill="#34A853"
                        />
                      </svg>
                    </span>
                    <div className="store-text">
                      <span className="small-text">GET IT ON</span>
                      <span className="large-text">Google Play</span>
                    </div>
                  </a>
                </li>
              )}

              {isValidUrl(repo.websiteUrl) && (
                <li
                  data-tooltip="Website"
                  style={{ "--bg": "#ff0000" }}
                  onClick={() => openInNewTab(repo.websiteUrl)}
                  role="button"
                  tabIndex={0}
                >
                  <a
                    href={repo.websiteUrl}
                    className="store-button website"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span className="store-icon">
                      <i className="fa-solid fa-globe"></i>
                    </span>
                    <div className="store-text">
                      <span className="small-text">Visit Our</span>
                      <span className="large-text">Website</span>
                    </div>
                  </a>
                </li>
              )}
            </ul>
          </div>
        </div>
      </Fade>
    </div>
  );
}
